<template>
    <div class="list_box">
        <div class="searchBox">
            <h3><span @click='goLast' class="icon-left"></span>
                <p>订单管理</p>
            </h3>
            <div class="searchInput">
                <div class="search_row">
                    <input class="my-search" v-model="value1" placeholder="输入设备号/店铺名/人物名搜索" />
                    <i @click="submitHandler" class="icon-search"></i>
                    <div class="menuBox" align="right">
                        <i class="icon-menu" @click="openclick()"></i>
                    </div>
                </div>
            </div>
        </div>
        <div class="info_list">
            <pull-to @infinite-scroll="getMoreList" class="file-lists">
                <ul class="pull_ul order_list">
                    <li v-for="item in dataList">
                        <div class="order_left">
                            <p class="sn">单号：{{item.sn}}</p>
                            <p>小工：{{item.to_user.name}}</p>
                            <p>支付者：{{item.user.name}}</p>
                            <p>店铺：{{item.store}}</p>
                            <p>是否结账：{{item.is_settle}}</p>
                        </div>
                        <div class="order_right">
                            <p>{{item.created_at}}</p>
                            <p class="money">&yen; {{item.amount|tofixTwo}}</p>
                        </div>
                    </li>
                </ul>
            </pull-to>
        </div>
        <div class="timeSearchJs" v-show="openbox">
            <div class="timeSearch_inner">
                <p style="margin-bottom:0">下级员工</p>
                <ul class="people">
                    <li v-for="item of lessList" @click="searchName(item)">{{item}}</li>
                </ul>
                <p>开始时间：</p>
                <div class="startTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="startTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
                <p>结束时间：</p>
                <div class="endTime">
                    <yd-cell-item>
                        <yd-datetime type="date" v-model="endTime" slot="right"></yd-datetime>
                    </yd-cell-item>
                </div>
            </div>
            <div class="searchBtn clearfixed">
                <button @click="submitHandler">搜  索</button>
            </div>
        </div>
    </div>
</template>
<script>
    import ajax from "../../ajax/ajax.js";
    import dataURL from "../../server/dataUrl.js";
    export default {
        data() {
            return {
                count: 0,
                page: 1,
                page_number: 10,
                startTime: "2009-01-01",
                endTime: "2009-01-01",
                openbox: false,
                value1: "",
                dataList: [],
                lessList: []
            }
        },
        methods: {
            searchName(name) {
                this.openbox = false;
                this.value1 = name;
                this.getInfo();
            },
            openclick() {
                this.openbox = !this.openbox;
            },
            goLast() {
                this.$router.go(-1);
            },
            submitHandler() {
                this.getInfo();
                this.openbox = false;
            },
            async getMoreList() {
                console.log("翻页");
                this.page = this.page + 1;
                let res = await ajax.Get(dataURL.main.order, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords: this.value1,
                    start_date: this.startTime != "2009-01-01" ? this.startTime + " 00:00:00": "",
                    end_date: this.endTime != "2009-01-01" ? this.endTime + " 23:59:59": "",
                })
                this.count = res.count;
                if (this.dataList.length >= res.count) {
                    this.page = this.page - 1;
                    this.$dialog.toast({
                        mes: "无更多数据",
                        timeout: 1500,
                        icon: "error"
                    });
                    return;
                }
                res.list.forEach(element => {
                    element.is_settle = element.is_settle=="yes"?"已结账":"未结账";
                });
                this.dataList = [...this.dataList, ...res.list];
            },
            getNextList(item) {
                this.$router.push({
                    path: "todayIncomeDetail",
                    query: { id: item.id }
                })
            },
            async getInfo() {
                let res = await ajax.Get(dataURL.main.order, {
                    page: this.page,
                    page_number: this.page_number,
                    keywords: this.value1,
                    start_date: this.startTime != "2009-01-01" ? this.startTime + " 00:00:00": "",
                    end_date: this.endTime != "2009-01-01" ? this.endTime + " 23:59:59": "",
                })
                res.list.forEach(element => {
                    element.is_settle = element.is_settle=="yes"?"已结账":"未结账";
                });
                this.dataList = res.list;
                console.log(res);
            },
            async getChild() {
                let res = await ajax.Get(dataURL.main.allChild)
                console.log(res);
                this.lessList = [];
                this.again(res);
            },
            again(date) {
                for (let i = 0; i < date.length; i++) {
                    this.lessList.push(date[i].name);
                    if (date[i].child.length > 0) {
                        this.again(date[i].child);
                    }
                }
            }
        },
        filters: {
            tofixTwo: function (value) {
                return (parseFloat(value) / 100).toFixed(2);
            }
        },
        created() {
            this.getInfo();
            this.getChild();
        }
    }
</script>


